import { Card } from "antd";
import React, { ReactElement } from "react";

export default function Responsibility(): ReactElement {
	return (
		<div className="Responsibility">
			<Card title="What are the primary responsibilities of radiation therapists?">
				<p>
					Radiation therapists do many things. Here are the some of the main
					responsibilities of radiation therapists:
					<ul>
						<li>
							Explain different treatments to the patient and answer any
							questions.
						</li>
						<li>
							Protect themselves and the patients from too much radiation.
						</li>
						<li>Find where to target the treatment.</li>
						<li>Calibrate the machinery.</li>
						<li>Monitor the patient and keep records.</li>
					</ul>
					These responsibilities and more allow a radiation therapists to take care of their patients.
				</p>
                <span>
                    <img src="https://www.bls.gov/ooh/images/2437.jpg"></img>
                </span>
			</Card>
		</div>
	);
}
