import { Card } from "antd";
import React, { ReactElement } from "react";

export default function Earning(): ReactElement {
	return (
		<Card title="What are the earnings and occupational outlooks for radiation therapists?">
			<p>
				The median wage for radiation therapists in 2019 was $85,560 a year.
				Employment opportunities are expected to increase by 7% from 2019-2029.
				Since more people are getting old, and old people are more likely to get
				cancer, this job will increase in demand.
			</p>
			<span>
				<img src="https://i.kym-cdn.com/photos/images/newsfeed/001/838/708/ae9.gif"></img>
				<img src="https://i.imgur.com/PPX3aEI.gif"></img>
			</span>
			<br />
			Also dave messed up on my coffee order by getting me decaf, so now I'm
			cranky.
		</Card>
	);
}
