import { Card } from "antd";
import React, { ReactElement } from "react";

export default function Home(): ReactElement {
	return (
		<div className="Home">
			<Card title="Welcome">
				<p>
					This website will help you get started with being a radiation
					therapist. You can click on the links above to go to that page like a
					normal website. I don't know why you would think they would do
					anything different. Anywho, This site was made react, react-router,
					and ant design. I spent a lot of time on this site, and I hope you see
					this as an A project. Also if you are still reading this, I have put a
					small loan of $1,000,000 under your chair.
				</p>
				<span>
					<img src="https://www.bls.gov/ooh/images/15428.jpg"></img>
					<img src="https://149469269.v2.pressablecdn.com/wp-content/uploads/2016/09/hero-medical-imaging-radiation-therapist.jpg"></img>
				</span>
			</Card>
		</div>
	);
}
