import { Card } from 'antd'
import React, { ReactElement } from 'react'

export default function Prep(): ReactElement {
    return (
        <Card title="How should someone interested in a radiation therapy career prepare?">
            <p>
                First, you need to pass Mr. Pulitzer's class because he teaches a lot of good vital information.
                After that, you need to get a license.
                But how do you get a license?
                I don't know that part. Sorry...
                <br/>
                <br/>
                Nah just kidding I do. First, you have to graduate a certified program.
                This program includes courses in different topics from how to operate the machinery,
                The theory behind radiation therapy, and other technical skills.
                After that is completed, which should only be hard if you're an absolute noob and
                not an epic gamer like me, you take a test and the the certificate.
            </p>
            <span>
                <img src="https://cdn.freeprintablecertificates.net/samples-free/Radiation_Treatment_Certificate.png"></img>
            </span>
        </Card>
    )
}
